B,qCAmBY,YAAa,CACb,WAAY,CApBxB,iCAwBQ,cAAe,CACf,kBAAmB,CAEnB,YAAa,CACb,kCAAmC,CA5B3C,oCA+BQ,iBAAkB,CAElB,YAAa,CACb,oCAAqC,CACrC,SAAU,CACb,iBCnCD,YAAa,CACb,qBAAsB,CACtB,OAAQ,CACX,iBCHG,YAAa,CACb,qBAAsB,CACtB,OAAQ,CAHZ,oCAMQ,YAAa,CACb,0BAA2B,CAC3B,OAAQ,CACX,wCCRD,oBAAqB,CACrB,iBAAkB,CAClB,YAAa,CACb,eAAgB,CAChB,kBAAmB,CACnB,2EAA4E,CAC5E,mFAAoF,CAPxF,gEAUQ,iBAAkB,CAClB,YAAa,CACb,oCAAqC,CAZ7C,kEAeY,gBAAiB,CACjB,cAAe,CACf,cAAe,CAClB,kBAKL,GACI,0BAA2B,CAAA,CCxBnC,QACI,iBAAkB,CAClB,gBAAiB,CACjB,mBAAoB,CACpB,qBAAsB,CACtB,UAAW,CALf,8BAQQ,wBAAyB,CAEzB,YAAa,CACb,iDAAkD,CAClD,SAAU,CAZlB,0CAeY,eAAgB,CAChB,4BAA6B,CAhBzC,4CAmBY,YAAa,CACb,cAAe,CApB3B,4BAyBQ,YAAa,CACb,iBAAkB,CAClB,UAAW,CACX,4BAA6B,CAC7B,6BAA8B,CAC9B,qBAAsB,CA9B9B,6CAiCY,aAAc,CAEd,YAAa,CACb,qCAAsC,CACtC,kBAAmB,CACnB,SAAU,CAtCtB,+CAyCgB,mBAAoB,CAzCpC,0DA4CgB,kBAAmB,CACnB,aAAc,CA7C9B,yDAgDgB,mBAAoB,CAhDpC,mDAoDY,wBAAyB,CApDrC,8DAuDY,6BAA8B,CACjC,eAKL,6BAA8B,CACjC,2BAGG,uCAA2C,CAC9C,gDAIG,wBAAyB","sourcesContent":["// CSS reset file\n\n// Set core body defaults\nbody {\n    height: 100vh;\n    line-height: 1.5;\n    text-rendering: optimizeSpeed;\n    -webkit-font-smoothing: antialiased;\n}\n\n// Remove default properties\n* {\n    margin: 0;\n    padding: 0;\n    outline: 0;\n    border: 0;\n    list-style: none;\n    text-decoration: none;\n}\n\n// Box sizing rules\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n\n// Improve media defaults\nimg, picture, video, canvas, svg {\n    max-width: 100%;\n    display: block;\n}\n\n// Inherit font for inputs and buttons\ninput, button, textarea, select {\n    font: inherit;\n}\n\n// Avoid text overflows\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n}\n\n// Set core root defaults\nhtml:focus-within {\n    scroll-behavior: smooth;\n}\n\n// Create a root stacking context\n#root, #__next {\n    isolation: isolate;\n}\n\n// Remove all animations, transitions and smooth scroll for people that prefer not to see them\n@media (prefers-reduced-motion: reduce) {\n    html:focus-within {\n        scroll-behavior: auto;\n    }\n\n    *, *::before, *::after {\n        animation-duration: 0.01ms !important;\n        animation-iteration-count: 1 !important;\n        transition-duration: 0.01ms !important;\n        scroll-behavior: auto !important;\n    }\n}","// Animations\n\n@keyframes slide {\n    from {\n        transform: translateX(0);\n    }\n    to {\n        transform: translateX(-100%);\n    }\n}\n","// Credit for implementing SCSS themes: https://medium.com/@dmitriy.borodiy/easy-color-theming-with-scss-bc38fd5734d1\n@import 'themes';\n\n:root {\n    $active-theme: '';\n    @each $property, $value in $active-theme {\n        --#{$property}: #{$value};\n    }\n}\n\nhtml[data-theme='light'] {\n    @each $property, $value in $light-theme {\n        --#{$property}: #{$value};\n    }\n}\n\nhtml[data-theme='dark'] {\n    @each $property, $value in $dark-theme {\n        --#{$property}: #{$value};\n    }\n}\n","// Default Variable Classes\n\n.hidden {\n    display: none;\n}\n\n// Font Sizes\n.fs-s {\n    font-size: 0.9rem;\n}\n\n.fs-m {\n    font-size: 1.2rem;\n}\n\n.fs-l {\n    font-size: 1.4rem;\n}\n\n.fs-xl {\n    font-size: 2rem;\n}\n\n.fs-xxl {\n    font-size: 3rem;\n}\n\n.fs-xxxl {\n    font-size: 5rem;\n}\n\n// Font Weights\n.fw-400 {\n    font-weight: 400;\n}\n\n.fw-500 {\n    font-weight: 500;\n}\n\n.fw-600 {\n    font-weight: 600;\n}\n\n.fw-700 {\n    font-weight: 700;\n}\n\n.test {\n    color: #969696;\n}\n\n// Greys\n.grey-300 {\n    color: #909090;\n}\n.grey-400 {\n    color: #808080;\n}\n.grey-500 {\n    color: #707070;\n}\n.grey-600 {\n    color: #606060;\n}\n.gery-700 {\n    color: #505050;\n}\n","// Forecast\n\n.dashboard.active {\n    display: grid !important;\n    grid-template-rows: repeat(6, max-content);\n    gap: 2em;\n\n    > :first-child {\n        margin-top: 2em;\n    }\n\n    > * {\n        min-height: 0;\n        min-width: 0;\n        max-width: 1000px;\n        width: 100%;\n        padding: 0 2em;\n        margin: 0 auto;\n    }\n}\n","// Error\n\n.error.active {\n    display: flex !important;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 0em;\n\n    p {\n        text-align: center;\n    }\n}\n","// Footer\n\nfooter {\n    grid-area: footer;\n    user-select: none;\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    gap: 0.5rem;\n\n    a {\n        color: inherit;\n        text-decoration: none;\n\n        .fa-github {\n            font-size: 22px;\n            transition: transform 0.3s ease-in-out;\n        }\n\n        .fa-github:hover {\n            transform: rotate(360deg) scale(1.2);\n        }\n    } \n}","// Grid\n\n$grid-header-height: 70px;\n$grid-footer-height: 70px;\n\nbody {\n    font-size: 16px;\n    font-weight: 300;\n    font-family: 'Poppins', sans-serif;\n    background-color: var(--bg-primary);\n}\n\n#grid-container {\n    height: max-content;\n    min-height: 100%;\n    max-width: 100%;\n\n    > * {\n        // Allow grid items to shrink beyond their contents' size\n        min-height: 0;\n        min-width: 0;\n    }\n}\n\n#grid-container.homepage--active {\n    display: grid;\n    grid-template-rows: 1fr $grid-footer-height;\n    grid-template-areas:\n        'content'\n        'footer';\n\n    color: var(--font-primary);\n    background-color: var(--bg-primary);\n\n    header,\n    .popular-locations {\n        display: none;\n    }\n}\n\n#grid-container.loading--active,\n#grid-container.error--active,\n#grid-container.dashboard--active {\n    color: var(--font-secondary);\n    background-color: var(--bg-secondary);\n\n    display: grid;\n    grid-template-rows: $grid-header-height max-content 1fr $grid-footer-height;\n    grid-template-areas:\n        'header'\n        'popular'\n        'content'\n        'footer';\n\n    > header,\n    .popular-locations {\n        color: var(--font-primary);\n        background-color: var(--bg-primary);\n    }\n    > header,\n    footer {\n        padding: 0 2em;\n    }\n}\n","// Header\n\nheader {\n    position: sticky;\n    top: 0;\n    left: 0;\n    z-index: 999;\n    user-select: none;\n    background-color: inherit;\n\n    display: grid;\n    grid-template-columns: 1fr max-content max-content;\n    grid-template-areas: 'logo search settings';\n    align-items: center;\n    gap: 2em;\n\n    .logo {\n        grid-area: logo;\n        cursor: pointer;\n    }\n\n    .search {\n        grid-area: search;\n    }\n\n    .settingsOpen {\n        grid-area: settings;\n        cursor: pointer;\n        font-size: 2em;\n        font-weight: 300;\n    }\n}\n","// Homepage\n\n.homepage.active {\n    display: flex !important;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 1.5em;\n\n    .logo {\n        position: relative;\n        height: max-content;\n        width: 200px;\n\n        img {\n            position: absolute;\n            bottom: 0;\n            left: -10%;\n        }\n\n        p {\n            position: absolute;\n            bottom: 0;\n            left: 28%;\n            font-size: 24px;\n            white-space: nowrap;\n        }\n    }\n\n    .settingsOpen {\n        cursor: pointer;\n        position: fixed;\n        top: 19px;\n        right: 32px;\n        font-size: 2em;\n        font-weight: 300;\n    }\n}\n","// Loading\n\n.loading.active {\n    display: flex !important;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 2em;\n\n    p {\n        text-align: center;\n    }\n    img {\n        height: 64px;\n        width: 64px;\n    }\n}\n","// Content\n\nmain {\n    grid-area: content;\n\n    > * {\n        display: none !important;\n        height: 100%;\n        width: 100%;\n    }\n}\n","// Media Queries\n\n@media only screen and (max-width: 768px) {\n    header {\n        .settingsOpen {\n            right: 32px !important;\n        }\n        .search {\n            display: none !important;\n        }\n    }\n\n    #settings {\n        .header > .search {\n            display: block !important;\n        }\n    }\n\n    .current-weather > .details-container {\n        grid-template-columns: repeat(3, 1fr) !important;\n    }\n\n    .day-card > .current-card > div > :first-child {\n        display: none;\n    }\n\n    .carousel__container {\n        --carousel-items: 5 !important;\n    }\n\n    .carousel__wrapper {\n        width: 85% !important;\n    }\n}\n\n@media only screen and (max-width: 640px) {\n    .day-card > .icon-container > span {\n        display: none;\n    }\n\n    .carousel__container {\n        --carousel-items: 4 !important;\n    }\n}\n\n@media only screen and (max-width: 540px) {\n    .popular-locations {\n        display: none;\n    }\n\n    .current-location {\n        padding-top: 1em !important;\n    }\n\n    .current-weather {\n        > .icon-container {\n            justify-self: center !important;\n        }\n        #currentTemp {\n            font-size: 4rem !important;\n        }\n        > .details-container {\n            grid-template-columns: repeat(2, 1fr) !important;\n        }\n    }\n\n    .carousel__wrapper {\n        width: 80% !important;\n    }\n\n    .carousel__container {\n        --carousel-items: 3 !important;\n    }\n}\n\n@media only screen and (max-width: 500px) {\n    #settings {\n        left: 0 !important;\n        width: 100% !important;\n        min-width: 375px;\n    }\n    .day-card > .current-card {\n        display: none;\n    }\n}\n\n@media only screen and (max-width: 440px) {\n    .carousel__wrapper {\n        width: 70% !important;\n    }\n    .carousel__container {\n        --carousel-items: 2 !important;\n    }\n}\n","// Popular Locations\n\n.popular-locations {\n    grid-area: popular;\n    padding: 0 2em;\n    overflow: hidden;\n}\n","// Settings modal\n\n#settings {\n    position: fixed;\n    left: calc(100% - 390px);\n    height: 100vh;\n    width: 390px;\n\n   